






































import { Vue, Component } from 'vue-property-decorator';
import Title from './Title.vue';
import Card from './Card.vue';
import * as echarts from 'echarts/core';
import {
  TooltipComponent,
  TooltipComponentOption,
  LegendComponent,
  LegendComponentOption,
} from 'echarts/components';
import { PieChart, PieSeriesOption } from 'echarts/charts';
import { LabelLayout } from 'echarts/features';
import { CanvasRenderer } from 'echarts/renderers';

echarts.use([
  TooltipComponent,
  LegendComponent,
  PieChart,
  CanvasRenderer,
  LabelLayout,
]);

type EChartsOption = echarts.ComposeOption<
  TooltipComponentOption | LegendComponentOption | PieSeriesOption
>;

const columns = [
  {
    title: '课题',
    dataIndex: 'column1',
    ellipsis: true,
    align: 'center',
  },
  {
    title: '产品代号',
    dataIndex: 'column2',
    ellipsis: true,
    align: 'center',
  },
  {
    title: '批次',
    dataIndex: 'column3',
    ellipsis: true,
    align: 'center',
  },
  {
    title: '实验进去度',
    dataIndex: 'column4',
    scopedSlots: { customRender: 'speed' },
    ellipsis: true,
    width: 190,
    align: 'center',
  },
];

@Component({
  components: { Title, Card },
})
export default class Part3 extends Vue {
  option: EChartsOption = {
    color: ['#8680d8', '#efd358', '#e86c60'],
    show: false,
    tooltip: { trigger: 'item' },

    legend: { show: false, width: 198, top: '2%', left: 'center' },
    series: [
      {
        type: 'pie',
        radius: '90%',
        center: ['50%', '50%'],
        itemStyle: {
          borderRadius: 5,
          borderColor: '#fff',
          borderWidth: 1,
        },
        label: {
          show: false,
        },
        emphasis: {
          label: {
            show: false,
          },
        },
        data: [
          { value: 1048, name: 'Search Engine' },
          { value: 735, name: 'Direct' },
          { value: 580, name: 'Email' },
        ],
      },
    ],
  };
  columns = columns;
  data = [
    { key: '1', column1: '1', column2: '1', column3: '1', column4: 90 },
    { key: '2', column1: '2', column2: '2', column3: '2', column4: 2 },
    { key: '3', column1: '3', column2: '3', column3: '3', column4: 12 },
    { key: '4', column1: '4', column2: '4', column3: '4', column4: 65 },
    { key: '5', column1: '5', column2: '5', column3: '5', column4: 74 },
    { key: '6', column1: '6', column2: '6', column3: '6', column4: 86 },
  ];
  mounted() {
    const charts = echarts.init(document.getElementById('pie') as HTMLElement);
    charts.setOption(this.option);
  }
}
